import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import { Box, Drawer, List, ListItem, ListItemIcon, ListItemText } from '@material-ui/core';
import DeveloperBoardIcon from '@material-ui/icons/DeveloperBoard';
import clsx from 'clsx';
import { Link, useHref, useNavigate } from 'react-router-dom';
import HomeIcon from '@material-ui/icons/Home';
import MyLocationIcon from '@material-ui/icons/MyLocation';
import BrushIcon from '@material-ui/icons/Brush';
import styled from 'styled-components';
import { useTranslation } from 'react-i18next';
import styles from './css/wapzhuye.module.css';

const CustomLink = styled(Box)`
   font-size:16px;
   color:black;
   text-decoration: none;
   padding-top:5px;
   padding-bottom:5px;
   font-weight: 400;
`
const MenuLog = styled.img`
  width:0.48rem;
  height:0.48rem;
`


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  }
}));
type Anchor = 'top' | 'left' | 'bottom' | 'right';

const ButtonAppBar: React.FC=({})=>{
  const { t } = useTranslation();
  const classes = useStyles();

  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor: Anchor, open: boolean) => (
    event: React.KeyboardEvent | React.MouseEvent,
  ) => {
    if (
      event.type === 'keydown' &&
      ((event as React.KeyboardEvent).key === 'Tab' ||
        (event as React.KeyboardEvent).key === 'Shift')
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const menu=[{
    label:"Learn",
    route:"/"
   },{
     label:"Developers",
     route:"/Developers"
   }
   ,
   {
     label:"Roadmap",
     route:"/Roadmap"
   },
   {
     label:"Participate",
     route:"/Developers"
   }
 ]
 const [active,setActive]=useState<any>(0);
 const navigate = useNavigate();
 const handActive=(idx:number)=>{
  setActive(idx);
  navigate(menu[idx].route);
}

  const list = (anchor: Anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        {menu.map((text, index) => (                  
                <ListItem button key={text.label} onClick={()=>{handActive(index)}} style={{'borderBottom':index===1?'1px solid #e1e1e1':''}}>
                  <ListItemIcon > 
                    
                    { text.label=='Learn'?<HomeIcon style={{"fontSize":'0.5rem'}} />:''}
                    { text.label=='Developers'?<MenuLog src='/image/750/home/user.png'/>:''}
                    { text.label=='Roadmap'?<MenuLog src='/image/750/home/guidepost.png'/>:''}
                    { text.label=='Participate'?<MenuLog src='/image/750/home/postion.png'/>:''}
                  </ListItemIcon>

                  <CustomLink   >{text.label}</CustomLink>
                </ListItem>
        ))}
        
      </List>
 
    </div>
  );

  return (
    <Box display={'flex'}>

          <img
            onClick={toggleDrawer('right', true)}
            className={`${styles['image_3']}`}
            src="https://codefun-proj-user-res-1256085488.cos.ap-guangzhou.myqcloud.com/63a7fdfb5a7e3f0310febbfd/63ad399a717ee30011337499/16722969855695721415.png"
          /> 
            <Drawer anchor={'right'} open={state['right']} onClose={toggleDrawer('right', false)}>
                 {list('right')}
                 <Box pb={2} display='flex'
                 flexDirection='column'
                 flex={1}
                 alignItems='center'
                 justifyContent='flex-end' 
                 color='#fff'>

                  </Box>
                  
            </Drawer>
    </Box>
  );
}
export default ButtonAppBar;
