import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import styled from 'styled-components';
import { Box } from '@material-ui/core';

const StylePanel=styled(Box)`
      width: '100%';
      display:flex ;
      justify-content: center;
        align-items: center;
        padding-top: 30px;
        font-size:24px;
      @media (max-width: 750px) {
         /* max-width: 95%; */
      }
      @media (min-width:750px){
        justify-content: center;
        align-items: center;
        padding-top: 30px;
        font-size:24px;
      }
`


const TextContent=styled(Box)`
    margin-top: 8px;
    border-left: 1px solid #bdbdbd;
    margin-left: 12px;
    padding-left: 20px;
    padding-right: 8px;
    pre{
        font-family: auto;
    }
`
const StyleStep=styled(Step)`

    @media (max-width: 750px) {
        .MuiStepIcon-text{
          font-size:0.23rem;
        }
        .MuiStepLabel-label{
            font-size:0.3rem;
        }
        .MuiStepIcon-root{
            font-size:0.45rem;
        }
        .MuiBox-root{
            max-width: 300px;
        }
    }
    @media (min-width:750px){
         
    }
    
`

const StylePre=styled.pre`
    @media (max-width: 750px) {
         font-size:0.18rem;
         overflow: auto;
    }
    @media (min-width:750px){
        font-size:16px;
    }
`

const StyleStepper=styled(Stepper)`
    @media (max-width: 750px) {
         max-width: 95%;
    }
    @media (min-width:750px){
         max-width: 80%;
    }
`




const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
      display: 'flex',
    /* height: 100%; */
      justifyContent: 'center',
      alignItems: 'center',
      paddingTop: '30px',
      fontSize:"24px"
    },
    vertical:{
        fontSize:"24px"
    },
    button: {
      marginTop: theme.spacing(1),
      marginRight: theme.spacing(1),
    },
    actionsContainer: {
      marginBottom: theme.spacing(2),
    },
    resetContainer: {
      padding: theme.spacing(3),
    },
  }),
);

function getSteps() {
  return ['### 2023 Q1', '### 2023 Q2', '### 2023 Q3', '### 2023 Q4'];
}

function getStepContent(step: number) {
  switch (step) {
    case 0:
      return `
* ElfinHost white paper release
* ElfinHost official website release
* Opensource ElfinHost's minimal environment
* Integrate the first CDN and Authorizer
* Application demo release
* Security audit`
      ;
    case 1:
      return `
* Storage supports Filecoin, Arweave, Swarm, etc.
* Integrate and promote more than 3 CDNs and Authorizer
* CDN and Authorizer reward and punishment mechanism
* CDN and Authorizer monitoring mechanism
* Economic model definition and implementation
      `;
    case 2:
      return `Try out different ad text to see what brings in the most customers,
              and learn how to enhance your ads using features like ad extensions.
              If you run into any problems with your ads, find out how to tell if
              they're running and how to resolve approval issues.`;
    default:
      return 'Unknown step';
  }
}
const Roadmap: React.FC = () => {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();
  const StepContent=[
    `
    * ElfinHost white paper release
    * ElfinHost official website release
    * Opensource ElfinHost's minimal environment
    * Integrate the first CDN and Authorizer
    * Application demo release
    * Security audit
    `,
    // ``,
    // "",
    // ""
    `
    * Storage supports Filecoin, Arweave, Swarm, etc.
    * Integrate and promote more than 3 CDNs and Authorizer
    * CDN and Authorizer reward and punishment mechanism
    * CDN and Authorizer monitoring mechanism
    * Economic model definition and implementation
    `,
    `
    * Support audio and video for multi-zone
    * Definition of copyright compliance DAO rules
    * More than 100 CDN and Authorizer nodes around the world
    `,
    `
    * Copyright Compliant DAO System Release
    * NAS and other personal data storage support
    * Self-built server Enclave environment support    
    `
  ]

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    <StylePanel>
      <StyleStepper  activeStep={activeStep} nonLinear={true} orientation="vertical">
        {steps.map((label, index) => (
          <StyleStep key={label} className={classes.vertical}>
            <StepLabel>
                {label}
            </StepLabel>
            {/* <StepContent> */}
              <TextContent>
                <StylePre>
                    {StepContent[index]}
                    {/* {getStepContent(index)} */}
                </StylePre>
                </TextContent>

            {/* </StepContent> */}

          </StyleStep>
        ))}
      </StyleStepper>
      {
      activeStep === steps.length && 
      (
        <Paper square elevation={0} className={classes.resetContainer}>
          <Typography>All steps completed - you&apos;re finished</Typography>
          <Button onClick={handleReset} className={classes.button}>
            Reset
          </Button>
        </Paper>
      )}
    </StylePanel>
  );
}
export default Roadmap;